<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="游戏">
            <el-input v-model.number="requestParam.search.game"></el-input>
          </el-form-item>
          <el-form-item label="房间">
            <el-input v-model.number="requestParam.search.room"></el-input>
          </el-form-item>
          <el-form-item label="局号">
            <el-input v-model.number="requestParam.search.round_id"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getList">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-table
          :data="page.list"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="game_id" label="游戏ID" align="center">
          </el-table-column>
          <el-table-column prop="game_name" label="游戏名称" align="center">
          </el-table-column>
          <el-table-column prop="room_id" label="房间Id" align="center">
          </el-table-column>
          <el-table-column prop="room_name" label="房间名称" align="center">
          </el-table-column>
          <el-table-column prop="round" label="局号" align="center">
          </el-table-column>
          <el-table-column
            prop="start_time"
            label="游戏开始时间"
            align="center"
          >
          </el-table-column>
          <el-table-column prop="end_time" label="游戏结束时间" align="center">
          </el-table-column>
          <el-table-column prop="round_flow" label="当局总流水" align="center">
          </el-table-column>
          <el-table-column prop="round_pump" label="当局总税收" align="center">
          </el-table-column>
          <el-table-column prop="uc" label="后台调整" align="center">
            <template slot-scope="scope">
              <el-popover placement="right" width="400" trigger="click">
                <ul>
                  <li>
                    受控:{{
                      scope.row.uc.c == null || scope.row.uc.c == false
                        ? "否"
                        : "是"
                    }}
                  </li>
                  <li>控制目标:{{ scope.row.uc.tar }}</li>
                  <li>控制等级:{{ scope.row.uc.level }}</li>
                  <li>控制进度:{{ scope.row.uc.now }}</li>
                  <li>
                    完成目标:{{
                      scope.row.uc.achieveTarget == null ||
                      scope.row.uc.achieveTarget == false
                        ? "否"
                        : "是"
                    }}
                  </li>
                </ul>
                <el-button type="primary" slot="reference">展示</el-button>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column label="详情" align="center" prop="opt">
            <template slot-scope="scope">
              <el-popover placement="right" width="400" trigger="click">
                <el-button type="primary" slot="reference">详情</el-button>
                {{ scope.row.opt }}
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
          v-if="pageshow"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        search: {
          game: null,
          room: null,
          round_id: null
        }
      },
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      },
      pageshow: true
    };
  },
  methods: {
    getList() {
      var that = this;
      that.page.list = [];
      that.page.total = 0;
      let query = {
        search: this.requestParam.search,
        page: {
          current_page: this.page.current_page,
          page_size: this.page.page_size
        }
      };
      this.$api.statistic.list_gameRecord(query).then(res => {
        if (res.data.code === 200) {
          that.pageshow = false; //让分页隐藏
          that.$nextTick(() => {
            //重新渲染分页
            that.pageshow = true;
          });
          that.page.total = res.data.data.total;
          that.page.list = res.data.data.list;
        }
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.getList();
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.getList();
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.getList();
      }
    }
  },
  filters: {
    formatDate: function(value) {
      return moment(value).format("YYYY/MM/DD hh:mm:ss a");
    },
    returnDefault: function(value) {
      if (!value) {
        return 0;
      }
    }
  }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerForm {
  padding: 2% 5%;
  border: #eef1f6 1px solid;
  margin-left: -100px;
  //box-shadow: #eef1f6 0px 0px 10px;
}
.innerForm_item {
  margin: 10px 0;
}
.dialog_input_small {
  width: 35%;
  margin: 0 20px;
}
.minInput {
  width: 40%;
}
.greenCor {
  font-size: larger;
  color: green;
}
.redCor {
  color: red;
}
</style>



